import { React, useEffect } from 'react';
import PropTypes from 'prop-types';

import s from './Modal.module.scss';

const Modal = ({ isOpen, onClose, children }) => {
	useEffect(() => {
		const handleEsc = (event) => {
			if (event.key === 'Escape') {
				onClose();
			}
		};

		if (isOpen) {
			window.addEventListener('keydown', handleEsc);
		}

		return () => {
			window.removeEventListener('keydown', handleEsc);
		};
	}, [isOpen, onClose]);

	if (!isOpen) return null;

	return (
		<div className={s.modal_overlay} onClick={onClose}>
			<div className={s.modal_content} onClick={(e) => e.stopPropagation()}>
				<button className={s.modal_close} onClick={onClose}>
				<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
					<path d="M18 6L6 18" stroke="#050505" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
					<path d="M6 6L18 18" stroke="#050505" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
				</svg>
				</button>
				{children}
			</div>
		</div>
	);
};

Modal.propTypes = {
	isOpen: PropTypes.bool.isRequired,
	onClose: PropTypes.func.isRequired,
	children: PropTypes.node.isRequired,
};

export default Modal;